import display from "./display.js";
import generateBtns from "./buttonsGen.js";
import calculate from "./calculate.js";
import clear from "./clear.js";
import del from "./delete.js";
import debugInput from "./debugInput.js";

const btnContainer = document.querySelector(".buttons-container");
const lScreen = document.querySelector(".low-screen");
const hScreen = document.querySelector(".high-screen");

let screen = {
  lScreen,
  hScreen,
  displayError: false,
  displayResult: false,
  ans: "",
};

const checkInput = (input) => {
  if (input == "=") {
    const expression = screen.lScreen.innerHTML;
    const result = calculate(screen);
    display(screen.hScreen, expression);
    display(screen.lScreen, result);
  } else if (input == "Clear") {
    clear(screen);
  } else if (input == "Del") {
    del(screen);
  } else if (input == "Ans") {
    const lastData = screen.lScreen.textContent;
    const data = lastData + "Ans";
    display(screen.lScreen, data);
  } else {
    if (
      screen.displayError ||
      (screen.displayResult && !["+", "x", "รท", "-"].includes(input))
    ) {
      clear(screen);
    }
    screen.displayResult = false;
    let newData = debugInput(input);

    const lastData = screen.lScreen.textContent;
    let data = lastData + newData;
    display(screen.lScreen, data);
  }
};

const clickEvent = (e) => {
  const input = e.target.innerHTML;
  if (e.target.localName == "section") return;

  checkInput(input);
};

generateBtns(btnContainer);

btnContainer.addEventListener("click", (e) => {
  clickEvent(e);
});

hScreen.addEventListener("click", (e) => {
  const content = e.target.textContent;
  clear(screen);
  display(lScreen, content);
});
